.all-section {
  width: 80%;
  margin: auto;
  text-align: center;
  margin-top: 120px;
}
.all-section h1 {
  font-size: 3rem;
  font-weight: 400;
  margin-bottom: 0;
}
.all-section .para {
  font-size: 20px;
  margin-bottom: 40px;
  color: #d1d2ff;
  margin-top: 15px;
}
.all-section .wrapper {
  display: inline-flex;
}
.all-section input {
  height: 45px;
  min-width: 300px;
  border: 3px solid #21b578;
  padding: 0 15px;
  background: transparent;
  color: #fff;
  font-size: 18px;
  color: #d1d2ff;
  font-weight: 700;
  border-radius: 4px 0 0 4px;
}
.all-section input:focus {
  outline: 0;
}
.all-section button {
  height: 51px;
  width: 130px;
  border: 2px solid #21b578;
  background: #21b578;
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  transition: .3s;
}
.all-section button:focus {
  outline: 0;
}
.all-section button:hover {
  background-color: #1ba068;
  border-color: #1ba068;
}
.text-container p {
  font-family: "Kalam", cursive;
  font-size: 22px;
}
.text-container {
  margin-top: 40px;
}
